.partners {
  margin-bottom: 17.4rem;
  padding-top: 2rem;
  position: relative;
  @include mobile {
    margin: 0;
    margin-bottom: 7rem;
  }
  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    left: -121rem;
    bottom: -19rem;
    width: 140rem;
    height: 140rem;

    background-image: url("./../img/lines/main3.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    transform: rotate(-90deg);

    @include mobile {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    right: -88.5rem;
    bottom: -56.5rem;
    width: 145rem;
    height: 145rem;

    background-image: url("./../img/lines/main3.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    transform: rotate(-180deg);

    @include mobile {
      width: 75rem;
      height: 75rem;
      right: -49.3rem;
      bottom: 10rem;
      transform: rotateX(180deg) rotateZ(111deg);
    }
  }
  &__title {
    margin-bottom: 4.4rem;

    @include mobile {
      margin-bottom: 2.5rem;
    }
  }
  &__slider {
    @include mobile {
      margin-bottom: 3rem;
    }
  }
  &__items {
    display: flex;
    // justify-content: space-between;
    flex-wrap: wrap;
    gap: 2rem;

    @include mobile {
      flex-wrap: nowrap;
      justify-content: flex-start;
      gap: 0;
    }

    .swiper-slide {
      width: auto;

      @include mobile {
        &:last-child {
          display: none;
        }
      }
    }
  }

  &__img {
    padding: 4rem 4.5rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.6rem;
    min-width: 22rem;
    max-width: 43rem;
    width: 100%;

    @include mobile {
      min-width: unset;
      max-width: unset;
      width: 100%;
      padding: 4rem 4.3rem;
      padding-top: 4.3rem;
    }
    &--mob {
      display: none;
      background-color: #fff;
      mask-image: url("./../img/partners/mob-mask.svg") !important;
      mask-repeat: no-repeat;
      mask-position: center;
      mask-size: 100% 100%;
      @include mobile {
        display: flex;
        padding-top: 3.9rem;
        padding-bottom: 5rem;
        gap: 0.1rem;
      }
    }
    img {
      max-height: 7.5rem;
      height: 100%;
      width: auto;
      object-fit: contain;

      @include mobile {
        max-height: 4.4rem;
      }
    }
    span {
      font-weight: 500;
      font-size: 2rem;
      line-height: 120%;
      text-align: center;
      color: var(--black);
    }
    a {
      font-weight: 500;
      font-size: 3.2rem;
      line-height: 120%;
      text-align: center;
      color: var(--black);
      transition: 0.2s ease-in;
      @include hover {
        color: var(--blue);
      }
      &:focus {
        color: var(--blue);
      }
      &:active {
        color: var(--blue-active);
      }
      @include mobile {
        font-size: 3rem;
      }
    }
  }

  .swiper-slide {
    mask-size: 100% 100%;
    mask-repeat: no-repeat;
    mask-position: center;
    background-color: #fff;
    height: 13rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 1.5rem;

    &:last-child {
      padding: 3.5rem 2rem;

      @include mobile {
        display: none;
      }
    }
    &:nth-child(1) {
      .partners__img {
        width: 25rem;

        img {
          max-height: 4rem;
        }
      }
    }
    &:nth-child(2) {
      .partners__img {
        width: 20rem;

        img {
          max-height: 4rem;
        }
      }
    }
    &:nth-child(3) {
      .partners__img {
        width: 22.2rem;

        img {
          max-height: 4rem;
        }
      }
    }
    &:nth-child(5) {
      flex: 1;
      @include mobile {
        flex: unset;
      }
      .partners__img {
        width: 35.9rem;

        img {
          max-height: 4rem;
        }
      }
    }
    &:nth-child(6) {
      .partners__img {
        width: 43.5rem;

        img {
          max-height: 4rem;
        }
      }
    }
    &:nth-child(7) {
      .partners__img {
        width: 28.8rem;
      }
    }
    &:nth-child(8) {
      .partners__img {
        width: 33.6rem;
      }
    }
    &:nth-child(9) {
      .partners__img {
        width: 32rem;
      }
    }
    &:nth-child(10) {
      .partners__img {
        width: 35rem;
      }
    }
    &:nth-child(11) {
      flex: 1;
      @include mobile {
        flex: unset;
      }
      .partners__img {
        width: 41rem;
      }
    }
    &:nth-child(12) {
      .partners__img {
        width: 27rem;
      }
    }
    &:nth-child(13) {
      .partners__img {
        width: 41rem;
      }
    }
    &:nth-child(14) {
      .partners__img {
        width: 26.2rem;
      }
    }
    &:nth-child(15) {
      .partners__img {
        width: 28.2rem;
      }
    }
    &:last-child {
      flex: 1;
      @include mobile {
        flex: unset;
      }
    }
    @include mobile {
      width: auto !important;
      height: 15.2rem !important;
      border-radius: 3rem;
      overflow: hidden;
    }
  }
}
